import Main from '@/components/main/Main'
import HelloWorld from '@/components/HelloWorld'

export default [
  {
    path: '/login',
    name: 'login',
    meta: {
      title: 'Login - ',
      hideInMenu: true
    },
    component: () => import('@/components/login/Login')
  },
  {
    path: '/hello',
    name: 'HelloWorld',
    component: HelloWorld
  },
  {
    path: '/',
    name: 'home',
    redirect: '/',
    component: Main,
    meta: {
      hideInMenu: true,
      notCache: true
    },
    children: [
      {
        path: '/',
        name: '',
        meta: {
          hideInMenu: true,
          notCache: true
        },
        component: () => import('@/components/home/Home')
      }
    ]
  },

  {
    path: '/publish',
    name: '',
    meta:{
      title: '',
    },
    component: Main,
    children: [
      {
        path: '/publish',
        name: 'publish',
        meta:{
          //<Icon type="paintbrush"></Icon>
          icon: 'android-create',
          title: '',
        },
        component: () => import('@/components/publish/Publish')
      },
    ]
  },

  {
    path: '/post',
    name: '',
    meta: {
      title: ''
    },
    component: Main,
    children: [
      {
        path: '/post',
        name: 'post',
        meta: {
          //<Icon type="checkmark-circled"></Icon>
          icon: 'ios-book',
          title: ''
        },
        component: () => import('@/components/post/PostList')
      }
    ]
  },

  {
    path: '/rank',
    name: 'rank',
    meta: {
      icon: 'podium',
      title: ''
    },
    component: Main,
    children: [
      {
        path: 'pv',
        name: 'pv',
        meta: {
          //<Icon type="checkmark-circled"></Icon>
          // icon: 'podium',
          title: ''
        },
        component: () => import('@/components/rank/PostPvList')
      },
      {
        path: 'likes',
        name: 'likes',
        meta: {
          //<Icon type="checkmark-circled"></Icon>
          // icon: 'podium',
          title: ''
        },
        component: () => import('@/components/rank/PostLikesList')
      },
      {
        path: 'comments',
        name: 'comments',
        meta: {
          //<Icon type="checkmark-circled"></Icon>
          // icon: 'podium',
          title: ''
        },
        component: () => import('@/components/rank/PostCommentsList')
      },
    ]
  },

  {
    path: '/topic',
    name: '',
    meta:{
      title: '',
    },
    component: Main,
    children: [
      {
        path: '/topic',
        name: 'topic',
        meta: {
          //<Icon type="checkmark-circled"></Icon>
          icon: 'android-bookmark',
          title: ''
        },
        component: () => import('@/components/topic/TopicList')
      },
    ]
  },

  {
    path: '/message-board',
    name: '',
    meta:{
      title: '',
    },
    component: Main,
    children: [
      {
        path: '/message-board',
        name: 'message-board',
        meta:{
          //<Icon type="paintbrush"></Icon>
          icon: 'chatbubble-working',
          title: '',
        },
        component: () => import('@/components/message-board/MessageBoard')
      },
    ]
  },

  {
    path: '/links',
    name: '',
    meta: {
      title: '',
      roles: ['ROLE_ADMIN']
    },
    component: Main,
    children: [
      {
        path: '/links',
        name: 'links',
        meta: {
          //<Icon type="checkmark-circled"></Icon>
          icon: 'link',
          title: ''
        },
        component: () => import('@/components/links/LinksList')
      }
    ]
  },

  {
    path: '/ip',
    name: 'IP',
    meta: {
      //<Icon type="android-compass"></Icon>
      icon: 'ios-world-outline',
      title: 'IP'
    },
    component: Main,
    children: [
      {
        path: 'ip_pool',
        name: 'ip_pool',
        meta: {
          //<Icon type="checkmark-circled"></Icon>
          // icon: 'document',
          title: 'IP'
        },
        component: () => import('@/components/ip/IpPool')
      },
      {
        path: 'ip_unrecognized',
        name: 'ip_unrecognized',
        meta: {
          //<Icon type="checkmark-circled"></Icon>
          // icon: 'document',
          title: 'IP'
        },
        component: () => import('@/components/ip/IpUnrecognized')
      },

    ]
  },
  {
    path: '/statistics',
    name: 'statistics',
    meta: {
      //<Icon type="android-compass"></Icon>
      icon: 'stats-bars',
      title: ''
    },
    component: Main,
    children: [
      {
        path: 'page_view',
        name: 'page_view',
        meta: {
          //<Icon type="checkmark-circled"></Icon>
          // icon: 'document',
          title: ''
        },
        component: () => import('@/components/statistics/PageView')
      },
      {
        path: 'search_stats',
        name: 'search_stats',
        meta: {
          //<Icon type="checkmark-circled"></Icon>
          // icon: 'document',
          title: ''
        },
        component: () => import('@/components/statistics/SearchStats')
      },
    ]
  },

  {
    path: '/administrator',
    name: '',
    meta: {
      title: '',
      roles: ['ROLE_ADMIN']
    },
    component: Main,
    children: [
      {
        path: '/administrator',
        name: 'administrator',
        meta: {
          //<Icon type="checkmark-circled"></Icon>
          icon: 'ios-people',
          title: ''
        },
        component: () => import('@/components/administrator/AdministratorList')
      }
    ]
  },

  {
    path: '/401',
    name: 'error_401',
    component: () => import('@/components/error-page/401.vue')
  },
  {
    path: '/500',
    name: 'error_500',
    component: () => import('@/components/error-page/500.vue')
  },
  {
    path: '*',
    name: 'error_404',
    component: () => import('@/components/error-page/404.vue')
  }
]



